import React, { useEffect, useState } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';
import axios from 'axios';

function LoginForm({ onSubmit }) {
  const [username, setUsername] = useState('user');
  const [password, setPassword] = useState('44cef368-919f-49d7-bf96-7bd5b639519e');

  const handleSubmit = (event) => {
    event.preventDefault();
    onSubmit(username, password);
  };

  return (
    <form onSubmit={handleSubmit}>
      <label>
        Username:
        <input type="text" value={username} onChange={(e) => setUsername(e.target.value)} />
      </label>
      <label>
        Password:
        <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
      </label>
      <button type="submit">Login</button>
    </form>
  );
}

function App() {
  const [chartData, setChartData] = useState([]);
  const [isLoggedIn, setIsLoggedIn] = useState(false);

  const fetchData = async (token) => {
    try {
      const config = {
        headers: { Authorization: `Bearer ${token}` },
      };

      const nhMonthlyResponse = await axios.get(
        'http://localhost:8080/api/northern-hemisphere-monthly/monthly',
        config
      );
      const shMonthlyResponse = await axios.get(
        'http://localhost:8080/api/southern-hemisphere-monthly/monthly',
        config
      );
      const nhMonthlyData = nhMonthlyResponse.data;
      const shMonthlyData = shMonthlyResponse.data;

      const processedData = nhMonthlyData.map((item, index) => {
        return {
          time: item.time,
          Global: (item.anomaly + shMonthlyData[index].anomaly) / 2,
          'Northern Hemisphere': item.anomaly,
          'Southern Hemisphere': shMonthlyData[index].anomaly,
        };
      });

      setChartData(processedData);
    } catch (error) {
      console.error('Error fetching data:', error);
    }
  };

  const handleLogin = async (username, password) => {
    try {
      const token = await authenticate(username, password);
      setIsLoggedIn(true);
      fetchData(token);
    } catch (error) {
      console.error('Error during login:', error);
    }
  };

  async function authenticate(username, password) {
    try {
      const response = await fetch('http://localhost:8080/', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ username, password }),
      });

      if (response.ok) {
        const data = await response.json();
        return data.token;
      } else {
        throw new Error('Authentication failed');
      }
    } catch (error) {
      console.error('Error during authentication:', error);
      throw error;
    }
  }

  return (
    <div className="App">
      <h1>Climate Change Data</h1>
      {isLoggedIn ? (
        <LineChart
          width={1000}
          height={500}
          data={chartData}
          margin={{
            top: 5,
            right: 30,
            left: 20,
            bottom: 5,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="time" />
          <YAxis />
          <Tooltip />
          <Legend />
         

          <Line type="monotone" dataKey="Global" stroke="blue" />
          <Line type="monotone" dataKey="Northern Hemisphere" stroke="green" />
          <Line type="monotone" dataKey="Southern Hemisphere" stroke="red" />
        </LineChart>
      ) : (
        <LoginForm onSubmit={handleLogin} />
      )}
      {/* Add links to data sources, and the description of the graph here */}
    </div>
  );
}




export default App;
